@import "../_bootstrap-sass/scss/bootstrap";
@import "variables";
@import "mixins";
@import "warnings";

@import "glyphs";
@import "sprite";
@import "base_structure";
@import "standard-content";
@import "print_bootstrap";
@import "print";

a.skip-content {
  position: absolute;
  width: 0;
  height: 0;
  padding: 0;
  margin: 0;
  overflow: hidden;
  border: none;
  color: $text-color-racing-green;
  &:focus {
    width: auto;
    height: auto;
    padding: 9px 10px;
    border: 1px solid;
    display: block;
    top: 0;
    left: 0;
    z-index: 9999999;
    outline: none;
  }
}

.skiptarget {
  width: 1px;
  height: 1px;
  overflow: hidden;
  position: absolute;
}

//Import all plugins
@import "plugins";

.reveal-item {
  visibility: hidden;
}