a.spotlight {
  color: $text-color-racing-green;
  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
    .spot-content {
      .spot-title {
        text-decoration: underline;
      }
    }
  }
}

.spotlight {
  background-color: $bg-color-white;
  .spot-content {
    .spot-label {
      font-size: 0.75rem;
      line-height: 16px;
      font-weight: 700;
      > * {
        color: inherit;
      }
    }
    .spot-title {
      font-size: 1rem;
      line-height: 22px;
      font-weight: 700;
    }
    p {
      font-size: 0.875rem;
      line-height: 21px;
    }
  }
  a {
    color: inherit;
    &:hover,
    &:focus,
    &:active{
      color: inherit;
    }
  }
}

@import "skin_border-bottom";
@import "skin_content-over-image";
@import "skin_fixed-lines";
@import "skin_hover-overlay";
@import "skin_image-overlay";
@import "skin_labels";
@import "skin_spotlight-overlay";