.event-list{
  &:before,
  &:after{
    display: table;
    content: "";
  }
  &:after{
    clear: both;
  }
}
.event-list-item{
  border-bottom: 1px solid $border-color-racing-green;
  .date{
    background-color: $bg-color-white-smoke;
    text-transform: uppercase;
    time{
      .month{
        font-size: 1.15rem;
        line-height: 1.3888888889;
      }
      .day{
        font-size: 2.05rem;
        line-height: 1.0303030303;
      }
    }
  }
  .info{
    h3{
      font-size: 1.15rem;
    }
    .event-description{
      span:before{
        content: "";
        @include use-glyphicon;
      }
      span.calendar:before{
        content: "\e109";
      }
      span.map:before{
        content: "\e062";
      }
    }
  }
}
