.multi-item-carousel {
  width: 100%;
  .carousel-inner {
    &:before,
    &:after {
      position: absolute;
      top: 0;
      content: "";
      width: 15px; //should be equal to padding
      height: 100%;
      z-index: 1;
    }
    &:before {
      left: 0;
    }
    &:after {
      right: 0;
    }
    .carousel-item {
      &.active,
      &.carousel-item-next,
      &.carousel-item-prev {
        @include flexbox;
        -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        align-items: stretch;
        > div {
          @include flexbox;
        }
      }
      .spotlight {
        width: 100%;
        display: block;
        margin-bottom: 12px;
        padding-bottom: 10px;
        position: relative;
        img {
          display: block;
          width: 100%;
          height: auto;
        }
        .overlay-icon-text {
          position: absolute;
          top: 0;
          padding: 5px 15px;
        }
      }
    }
  }
  .control {
    position: absolute;
    width: 22px;
    height: 22px;
    top: 0;
    bottom: 0;
    margin: auto;
    z-index: 2;
    &.left {
      left: -15px;
      right: auto;
    }
    &.right {
      left: auto;
      right: -15px;
    }
  }
}
@media screen and (min-width: $screen-sm) and (max-width: $screen-md) {
  .multi-item-carousel {
    .carousel-item {
      width: 100%;
    }
  }
}
.no-carousel {
  .carousel-inner {
    &:before,
    &:after {
      display: none;
    }
    .carousel-item {
      margin-left: -15px;
      margin-right: -15px;
      display: block;
      width: auto;
      .no-img {
        .spotlight {
          padding-top: 10px;
          img,
          p {
            display: none;
          }
        }
      }
    }
  }
  .control {
    display: none;
  }
}
/*The following styling is added using javascript*/
/*if you are using only column size for all multi-item-carousels
in your site then exclude JS code and use this SCSS in your stylesheet*/
/*please update col-size according to your column size*/

/*
carousel-item active carousel-item-left
carousel-item carousel-item-next carousel-item-left

carousel-item carousel-item-prev carousel-item-right
carousel-item active carousel-item-right
*/

@mixin multiSliderAnimate($items_in_slide) {
  $col-size: 100% / $items_in_slide;
  .multi-item-carousel {
    &[data-item-per-slide="#{$items_in_slide}"] {
      .carousel-inner {
        .carousel-item,
        .carousel-item.active {
          transition: 0.5s ease-in-out all;
          @media all and (transform-3d), (-webkit-transform-3d) {
            // use your favourite prefixer here
            transition: 0.5s ease-in-out all;
            backface-visibility: visible;
          }
        }
        .carousel-item {
          &.carousel-item-next,
          &.carousel-item-prev,
          &.carousel-item-next.carousel-item-left,
          &.carousel-item-prev.carousel-item-right,
          &.active.carousel-item-right,
          &.active.carousel-item-left {
            transform: translate3d(0, 0, 0);
          }
          &.active.carousel-item-left,
          &.carousel-item-prev {
            transform: translate3d(-$col-size, 0, 0);
          }
          &.active.carousel-item-right,
          &.carousel-item-next {
            transform: translate3d($col-size, 0, 0);
          }
        }
      }
    }
  }
}

@include multiSliderAnimate(4);
@include multiSliderAnimate(3);
