.carousel-banner {
  .container {
    position: relative;
  }
  .carousel-indicators {
    li {
      height: 15px;
      width: 15px;
      margin: 0 14px 0 0;
    }
  }
  .arrow-control,
  .play-pause {
    position: absolute;
    top: 202px;
    display: block;
    height: 46px;
    width: 25px;
    z-index: 999;
    @media screen and (max-width: $screen-xs) {
      top: 62px;
    }
  }
  .arrow-control {
    &.left {
      left: 16px;
      @media screen and (max-width: $screen-xs) {
        width: 13px;
      }
    }
    &.right {
      right: 16px;
      @media screen and (max-width: $screen-xs) {
        width: 13px;
      }
    }
  }
  .play-pause {
    height: 30px;
    width: 30px;
    span {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      width: 14px;
      height: 16px;
    }
    &.play-control {
      top: 18px;
      right: 53px;
    }
    &.pause-control {
      top: 18px;
      right: 16px;
    }
  }
  .carousel-inner {
    .carousel-item {
      img {
        display: block;
        width: auto;
        height: auto;
        @media screen and (max-width: $screen-xs) {
          width: 100%;
        }
      }
      .carousel-caption {
        top: 137px;
        right: auto;
        bottom: auto;
        left: 198px;
        max-width: 600px;
        width: 100%;
        padding: 15px 0;
        margin: 0;
        @media screen and (max-width: $screen-xs) {
          max-width: 100%;
          top: 40px;
          left: 0;
          padding: 15px 15px;
        }
        .h-style {
          display: block;
        }
        .btn {
          padding: 9px 45px;
          @media screen and (max-width: $screen-xs) {
            display: none;
          }
        }
        a:hover{
          text-decoration:none;
        }
      }
    }
  }
}

.carousel-fade {
  .carousel-inner {
    .carousel-item {
      transition-property: opacity;
    }
    .carousel-item,
    .active.carousel-item-left,
    .active.carousel-item-right {
      opacity: 0;
    }
    .active,
    .carousel-item-next.carousel-item-left,
    .carousel-item-prev.carousel-item-right {
      opacity: 1;
    }
    .carousel-item-next,
    .carousel-item-prev,
    .active.carousel-item-left,
    .active.carousel-item-right {
      left: 0;
      transform: translate3d(0, 0, 0);
    }
  }
}