#nav_drop_down {
  background-color: $nav-header-bg-light-color;
  #nav_drop_down_box {
    border-bottom: 1px solid $nav-border-color;
  }
  .mobile-menu {
    text-align: right;
    button {
      border: none;
      background-color: transparent;
    }
    #user_drop_down {
      background: url("imgs/navigation-dropdowns/user-icon-big.png") no-repeat center center;
    }
    #menu_drop_down {
      background: url("imgs/navigation-dropdowns/menu-icon.png") no-repeat center center;
      &.open {
        background: url("imgs/navigation-dropdowns/cross-icon.png") no-repeat center center;
      }
    }
  }
  .nav-column {
    text-align: right;
    @media screen and (max-width: $screen-xs) {
      text-align: left;
      background-color: $nav-header-bg-light-color;
    }
  }
}

#tns_drop_down {
  > li {
    border-right: 1px solid $nav-border-color;
    @media screen and (max-width: $screen-xs) {
      border-right: none;
    }
    a {
      color: $tns-link-color;
      font-size: 0.75rem;
      font-weight: 400;
      border-bottom: 1px solid transparent;
      @media screen and (max-width: $screen-xs) {
        font-size: 0.8125rem;
        line-height: 22px;
        border-bottom: none;
      }
      &:hover,
      &:focus,
      &:active {
        text-decoration: none;
        border-bottom: 1px solid $nav-border-color;
      }
    }
    &.call-back {
      @media screen and (max-width: $screen-xs) {
        border-top: 1px solid $nav-border-color;
        border-bottom: 1px solid $nav-border-color;
      }
      a {
        @media screen and (max-width: $screen-xs) {
          background-color: $nav-header-link-active-color;
          color: $nav-header-bg-color;
          font-weight: 600;
          font-size: 0.875rem;
          line-height: 34px;
          text-align: center;
        }
      }
    }
    &:last-of-type {
      border-right: none;
    }
    &.active {
      a {
        color: $nav-header-link-active-color;
        &:hover,
        &:focus,
        &:active {
          border-color: inherit;
        }
      }
    }
  }
}

#gns_drop_down {
  li {
    a {
      color: $nav-header-link-color;
    }
  }
  > li {
    @media screen and (max-width: $screen-xs) {
      border-bottom: 1px solid $nav-border-color;
    }
    > a {
      font-size: 0.9375rem;
      font-weight: 700;
      text-transform: uppercase;
      border-bottom: 1px solid transparent;
      &:hover,
      &:focus,
      &:active {
        text-decoration: none;
        border-bottom: 1px solid $nav-border-color;
        @media screen and (max-width: $screen-xs) {
          border-bottom: 1px solid transparent;
        }
      }
      @media screen and (max-width: $screen-xs) {
        line-height: 30px;
      }
    }

    &.dropdown {
      > a {
        span.down-arrow {
          background: url("imgs/navigation-dropdowns/arrow-down-menu.png") no-repeat center center;
        }
      }
      .dropdown-menu {
        box-shadow: 0 2px 1px 0 rgba(0, 0, 0, 0.24);
        border-radius: 0;
        &:after {
          border: 10px solid transparent;
          border-bottom-color: $nav-header-bg-color;
        }
        @media screen and (max-width: $screen-xs) {
          box-shadow: none;
          background-color: transparent;
          border: none;
        }
        > li {
          > a {
            color: $nav-header-link-color;
            font-size: 0.8125rem;
            //font-weight: 400;
            line-height: 16px;
            text-align: center;
            @media screen and (max-width: $screen-xs) {
              text-align: left;
              font-size: 0.875rem;
              font-weight: 600;
            }
            &:hover,
            &:focus,
            &:active {
              text-decoration: underline;
            }
          }
          > ul {
            > li {
              > a {
                font-size: 0.75rem;
                line-height: 16px;
              }
            }
          }
        }
      }
      &.open {
        > a {
          border-bottom: 1px solid $nav-border-color;
          @media screen and (max-width: $screen-xs) {
            border-bottom: none;
          }
        }
      }
    }
    &.active {
      a {
        color: $nav-header-link-active-color;
        &:hover,
        &:focus,
        &:active {
          border-color: inherit;
        }
      }
    }
  }
}

@import "skin_expand_hover";
@import "skin_search_menu";