#nav_drop_down {
  #gns_drop_down.separate-links {
    > li {
      @media screen and (max-width: $screen-xs) {
        width: 100%;
      }
    }
    li.dropdown {
      padding-right: 15px;
      @media screen and (max-width: $screen-xs) {
        padding-bottom: 5px;
      }
      > a {
        @media screen and (max-width: $screen-xs) {
          display: inline-block;
        }
        &.link-item {
          @media screen and (max-width: $screen-xs) {
            width: calc(100% - 40px);
          }
        }
        &.dropdown-arrow {
          width: 12px;
          margin-left: 3px;
          display: inline-block;
          @media screen and (max-width: $screen-xs) {
            width: 40px;
            margin-left: 0;
          }
          span.down-arrow {
            right: 0;
            left: 0;
            @media screen and (max-width: $screen-xs) {
              left: auto;
            }
          }
        }
      }
      .dropdown-menu {
        top: 35px;
        @media screen and (max-width: $screen-xs) {
          top: 0;
        }
      }
      &.open {
        > a.dropdown-arrow {
          &:hover,
          &:focus,
          &:active {
            text-decoration: none;
            border-bottom: 1px solid $nav-border-color;
            @media screen and (max-width: $screen-xs) {
              border-bottom: 1px solid transparent;
            }
          }
        }
      }
    }
    &:last-of-type {
      &.dropdown {
        padding-right: 0;
        @media screen and (max-width: $screen-xs) {
          padding-right: 15px;
        }
      }
    }
  }
  &.expand-on-touch {
    #tns_drop_down {
      margin-bottom: 10px;
    }
    #gns_drop_down {
      > li {
        > a {
          display: inline-block;
          padding-top: 20px;
          padding-bottom: 20px;
          position: relative;
          border-bottom: none;
          @media screen and (max-width: $screen-xs) {
            padding-top: 5px;
            padding-bottom: 5px;
          }
        }
        &.show.tapped{
          @media only screen and (device-width: 768px) {
            .dropdown-menu {
              display: block !important;
            }
          }
        }
        .dropdown-menu {
          top: 55px;
          @media screen and (max-width: $screen-xs) {
            top: 0;
          }

          li.overview-link { /* For IPAD general iPad layouts */
            @media screen and (min-width: $screen-sm) {
              display:none;
            }
            @media only screen and (device-width: 768px) {
              display: block !important;
            }
          }
        }
      }
      &:not(.ipad) {
        > li {
          > a {
            &:hover,
            &:focus,
            &:active {
              border-bottom: none;
              &:before {
                content: "";
                width: 100%;
                height: 1px;
                position: absolute;
                left: 0;
                top: 40px;
              }
            }
          }
          &:hover {
            @media screen and (min-width: $screen-sm) {
              > a span.down-arrow {
                transform: rotate(-180deg);
              }
              > .dropdown-menu {
                display: block;
                li.overview-link {
                  display: none;
                }
              }
            }
          }
          &.tapped,
          &.tapped:hover {
            //@media screen and (min-width: $screen-sm) {
            > .dropdown-menu {
              li.overview-link {
                display: block;
              }
            }
            //}
          }
        }
      }
    }
    .nav-column.search-box-item .search-box {
      margin-top: 10px;
    }
  }
}