.event-list-item{
  padding: 30px 0;
  @media screen and (max-width: $screen-xs) {
    padding-bottom: 15px;
  }
  .date{
    width: 10%;
    flex: 0 0 10%;
    max-width: 10%;
    padding-left: 0;
    padding-right: 0;
    text-align: center;
    @media screen and (max-width: $screen-xs) {
      width: 100%;
      flex: 0 0 100%;
      max-width: 100%;
      margin-bottom: 20px;
    }
    time{
      padding: 18px 0 10px;
      display: block;
      .month,
      .day{
        display: block;
        padding-bottom: 4px;
      }
    }
  }
  .info{
    width: 90%;
    flex: 0 0 90%;
    max-width: 90%;
    padding-left: 25px;
    padding-right: 0;
    @media screen and (max-width: $screen-xs) {
      width: 100%;
      flex: 0 0 100%;
      max-width: 100%;
      padding-left: 0;
    }
    h3{
      margin: 0;
      padding: 0 0 8px;
    }
    .event-description{
      margin-bottom: 0;
      padding-bottom: 0;
      span{
        display: inline-block;
        position: relative;
        padding-left: 25px;
        width: 100%;
        max-width: 100%;
        margin-bottom: 7px;
      }
      span p{
        display: inline-block;
        @media screen and (max-width: $screen-xs) {
          margin-bottom: 0;
        }
      }
      span:before{
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
}
