a.spotlight {
  cursor: pointer;
  &:hover,
  &:focus,
  &:active {
    outline: none;
  }
}

.spotlight {
  display: block;
  margin: 0 0 15px;
  .image-wrapper {
    display: block;
    position: relative;
  }
  img {
    display: block;
    width: 100%;
    height: auto;
  }
  .spot-content {
    padding: 10px 10px;
    .spot-label {
      display: inline-block;
      // if label has icons, we can remove it label tag doesn't have icons
      > * {
        margin-right: 5px;
      }
    }
    .spot-title {
      display: block;
      margin: 10px 0 0;
      &:first-child {
        margin-top: 0;
      }
    }
    p {
      display: block;
      margin: 10px 0 0;
    }
  }
  a {
    &:hover,
    &:focus,
    &:active {
      outline: none;
    }
  }
}

// hide label(s) and description tag because we will show only title for this class
.spotlight.spotlight-minimal {
  .spot-content {
    .spot-title {
      margin: 0;
    }
    p,
    .spot-label {
      display: none !important;
    }
  }
}


@import "structure_content-over-image";
@import "structure_horizontal";
@import "structure_hover-overlay";
@import "structure_image-overlay";
@import "structure_labels";
@import "structure_spotlight-overlay";
@import "structure_video-spot";