/*Use these class with @extend i.e

.sample-box {
  @extend .svg-social_facebook
  &:after {
  @extend .svg-social_facebook
  }
}

Where ever you want to use it, because its give benefit to easily add new OR remove icons from sprite
just replace your svg sprite and modify these classes.

*/
.svg-social_facebook {
  background: url("svg/sprite01.svg") 0 0 no-repeat;
  width: 30px;
  height: 30px;
}
.svg-social_google {
  background: url("svg/sprite01.svg") 50% 0 no-repeat;
  width: 30px;
  height: 30px;
}

.svg-social_linkedin {
  background: url("svg/sprite01.svg") 0 100% no-repeat;
  width: 30px;
  height: 30px;
}

.svg-social_twitter {
  background: url("svg/sprite01.svg") 50% 100% no-repeat;
  width: 30px;
  height: 30px;
}

.svg-social_youtube {
  background: url("svg/sprite01.svg") 100% 0 no-repeat;
  width: 30px;
  height: 30px;
}

