.annotated-spot {
  .annotated-bg {
    @media screen and (max-width: $screen-xs) {
      height: auto;
    }
    .pins-wrapper {
      position: relative;
      .map-pins {
        position: relative;
        height: 613px;
        > span {
          height: 21px;
          width: 21px;
          @media only screen and (device-width: 768px) {
            height: 26px;
            width: 26px;
          }
          position: absolute;
          left: 0;
          top: 0;
        }
        .map-tip .tooltip-inner {
          padding: 0;
          max-width: none;
          position: relative;
          &:before {
            position: absolute;
            width: 8px;
            height: 14px;
            margin: auto;
            top: 0;
            right: auto;
            bottom: 0;
            left: -8px;
          }
        }
        .map-tip .map-pin {
          display: table;
          width: 100%;
          min-height: 41px;
          span {
            display: table-cell;
            vertical-align: middle;
            text-align: right;
            &:first-of-type {
              padding: 0 15px;
            }
            &:last-of-type {
              width: 81px;
              min-width: 81px;
              max-width: 81px;
              position: relative;
              &:before {
                position: absolute;
                width: 8px;
                height: 14px;
                margin: auto;
                top: 0;
                right: auto;
                bottom: 0;
                left: -1px;
              }
            }
          }
          img {
            width: auto;
            height: auto;
            min-width: 81px;
            min-height: 42px;
            max-width: 81px;
            max-height: 42px;
            margin-bottom: -5px;
          }
        }
      }
    }
    .collapseable-wrapper {
      margin-bottom: 22px;
      ul {
        padding: 0;
        margin: 0;
        list-style: none;
        li {
          @media screen and (max-width: $screen-xs) {
            margin-top: 14px;
            margin-bottom: 0;
          }
          a {
            width: 100%;
            display: inline-block;
            position: relative;
            line-height: 1.17647;
            padding: 15px 0 15px 35px;
            font-size: 1rem;
            &:before {
              content: "";
              background: url("imgs/interactive-annotated-image-grouped-hotspot/oh-sprite-icons.png") no-repeat;
              background-position: -241px -1px;
              width: 12px;
              height: 15px;
              position: absolute;
              margin: auto;
              top: 0;
              right: auto;
              bottom: 0;
              left: 10px;
            }
          }
        }
      }
    }
  }
  .pin-details {
    display: none;
    margin-top: 30px;
    padding-bottom: 65px;
    padding-top: 20px;
    @media screen and (max-width: $screen-xs) {
      padding-right: 30px;
      padding-left: 30px;
    }
    .pin-details-header {
      float: left;
      width: 100%;
      .name {
        float: left;
      }
      .place {
        float: left;
      }
    }
    .image {
      max-width: 445px;
      width: 100%;
      float: left;
      margin-right: 30px;
      img {
        width: 100%;
      }
    }
    .description {
      float: left;
      max-width: 635px;
    }
  }
}
