#tns_drop_down li.dropdown {
  .dropdown-menu {
    left: auto;
    right: 0;
    li {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}

#nav_drop_down .nav-column #tns_drop_down > li.dropdown {
  display: inline-block;
  @media screen and (max-width: $screen-xs) {
    display: none;
  }
}

#nav_drop_down .nav-column.search-box-item {
  font-size: 0;
  #gns_drop_down,
  .search-box {
    vertical-align: top;
    display: inline-block;
  }
  #gns_drop_down {
    width: calc(100% - 180px);
    padding-right: 15px;
    @media screen and (max-width: $screen-xs) {
      width: 100%;
      padding-right: 0;
    }
  }
  .search-box {
    margin: -10px 0 0;
    padding: 0;
    width: 180px;
    font-size: 0;
    @media screen and (max-width: $screen-xs) {
      width: 90%;
      margin: 10px auto;
    }

    input {
      height: 40px;
      margin-bottom: 0;
      border: none;
      display: inline-block;
      vertical-align: bottom;
    }

    input[type="text"] {
      display: inline-block;
      width: 75%;
      margin: 0;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }

    .btn {
      display: inline-block;
      width: 25%;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
  }
}