@import "variables";

/*#region Footer Items*/
footer {
  background-color: $bg-color-deep-sky-blue;
  padding: 15px 0;
  .footer-menu {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      display: inline-block;
      padding: 0 8px;
      @media screen and (max-width: $screen-xs) {
        width: 100%;
        padding-left: 0;
      }
      &:after {
        padding-left: 2px;
      }
      &:first-of-type {
        padding-left: 0;
      }
      a {
        color: $white;
        @include x-rem(font-size, 14px);
        @include line-height(14, 38);
        @media screen and (max-width: $screen-xs) {
          @include line-height(14, 30);
        }
      }
    }
  }
}

.signup-newsletter {
  margin-bottom: 10px;
  input {
    margin-bottom: 10px;
    height: 40px;
    @include placeHolderTextColor($white);
    &.form-control::-webkit-input-placeholder {
      color: $text-color-dim-gray;
    }
  }
  .input-group {
    .input-group-btn {
      &:after {
        @include use-glyphicon;
        text-rendering: auto;
        font-size: 0.875rem;
        color: $white;
        display: inline-block;
        content: "\e092";
        position: absolute;
        z-index: 145;
        right: 15px;
        top: 14px;
      }
    }
    input,
    .btn {
      width: 110px;
      padding-right: 30px;
      border-radius: 0;
      border: none;
      &:focus,
      &:active,
      &:hover {
        box-shadow: 0 0 0;
      }
    }
  }

  .error {
    display: none;
  }
  .success {
    display: block;
  }
  .alert[style*="inline"] {
    display: block !important;
  }
}

/*#endregion*/

.standard-content {
  h3 {
    &.example {
      color: $text-color-deep-sky-blue;
      padding-top: 30px;
      padding-bottom: 10px;
      border-bottom: 1px solid $border-color-silver;
      margin-bottom: 15px;
      @media screen and (max-width: $screen-xs) {
        padding-top: 10px;
        margin-bottom: 10px;
      }
    }
  }
  .media-download {
    .file-type {
      margin-right: 5px;
    }
  }
  a {
    &.external-link {
      &:after {
        background-position: -100px -108px;
      }
    }
  }

  .file-download-link {
    text-transform: uppercase;
    padding: 25px 0 25px 42px;
    position: relative;
    &:last-of-type {
      margin-bottom: 30px;
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 0;
      }
    }
    a {
      font-weight: 600;
      &:before {
        display: inline-block;
        @include use-fontawsome(24px);
        content: "\f15b";
        position: absolute;
        left: 2px;
        top: 20px;
      }
      &:hover,
      &:focus,
      &:active {
        strong {
          text-decoration: underline;
        }
      }
    }
    a[href$=".pdf"]:before {
      content: "\f1c1";
    }
    a[href$=".doc"]:before,
    a[href$=".docx"]:before {
      content: "\f1c2";
    }
    a[href$=".xls"]:before,
    a[href$=".xlsx"]:before {
      content: "\f1c3";
    }
    a[href$=".txt"]:before {
      content: "\f15c";
    }
    span {
      display: inline-block;
      padding-right: 5px;
      text-transform: none;
      font-weight: 500;
      @media screen and (max-width: $screen-xs) {
        display: block;
      }
    }
  }
}

.media-listing {
  .media {
    padding: 0;
    margin: 0 0 30px;
    @media screen and (max-width: $screen-xs) {
      padding: 0 0 30px;
      display: block;
    }
    .media-left {
      padding-right: 27px;
      @media screen and (max-width: $screen-xs) {
        display: block;
        padding-right: 0;
      }
      img {
        @media screen and (max-width: $screen-xs) {
          display: block;
          width: 100%;
          height: auto;
        }
      }
    }
    .media-body {
      width: 100%;
      @media screen and (max-width: $screen-xs) {
        display: block;
        width: 100%;
      }
      .media-heading {
        color: $text-color-dim-gray;
        @include line-height(18, 20);
        margin-bottom: 10px;
        p {
          color: $text-color-racing-green;
          @include x-rem(font-size, 14px);
          @include line-height(14, 22);
        }
      }
    }
    &:hover,
    &:focus,
    &:active {
      text-decoration: none;
      .media-heading {
        text-decoration: underline;
      }
      p {
        text-decoration: none;
      }
    }
  }
  &.no-description {
    .media {
      .media-body {
        p {
          display: none;
        }
      }
    }
  }
}

.no-carousel {
  .carousel-inner {
    .carousel-item {
      .no-img {
        .spotlight {
          @media screen and (max-width: $screen-xs) {
            border-top: 2px solid $border-color-silver;
            padding: 0;
          }
        }
      }
    }
  }
}

.sorting-wrapper {
  border-bottom: 2px solid #ececf0;
  .btn {
    margin-right: 15px;
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 15px;
      margin-right: 0;
      width: 48%;
      &:last-child {
        float: right;
      }
    }
  }
}

// bg-color class for the section to give grey background
.bg-light {
  background-color: $bg-color-white-smoke !important;
}

/*#region Sitemap Page*/
.site-map ul,
.CMSSiteMapList {
  padding: 0 0 0 20px;
  li {
    padding: 10px 0;
    a {
      display: inline-block;
    }
    ul {
      padding-top: 10px;
    }
    &:last-of-type {
      padding-bottom: 0;
    }
  }
}

/*#endregion end*/

/*#region Home Page Items*/
.video-spot-wrapper {
  .box {
    padding-right: 45px;
    @media screen and (max-width: $screen-xs) {
      padding-right: 15px;
    }
    .video-box {
      width: 63.5%;
      padding-right: 0;
    }
    .join-box {
      width: 36.5%;
      @media screen and (max-width: $screen-xs) {
        width: 100%;
        margin-bottom: 15px;
      }
    }
  }
  .grey-box {
    background-color: $bg-color-white-smoke;
    padding: 15px 15px;
    p {
      @include x-rem(font-size, 14px);
      @include line-height(14, 22);
      color: $text-color-racing-green;
      margin: 0 0 20px;
      padding-bottom: 0;
      &:last-of-type {
        margin-bottom: 0;
      }
      a {
        display: block;
        width: 100%;
        padding: 5px 10px;
        text-align: center;
        background-color: $bg-color-racing-green;
        @include x-rem(font-size, 12px);
        @include line-height(12, 26);
        color: $white;
        text-transform: uppercase;
        border-radius: 0;
        border: none;
        box-shadow: 0 0 0 0;
        @media screen and (max-width: $screen-xs) {
          @include x-rem(font-size, 14px);
          @include line-height(14, 20);
          margin: 30px auto 0;
        }
      }
    }
  }
  .keep-in-touch {
    z-index: 1;
    position: relative;
    &:after {
      content: "";
      background: url("imgs/oh-sprite-icons.png") no-repeat;
      background-position: -483px 0;
      z-index: -1;
      width: 116px;
      height: 116px;
      position: absolute;
      top: 30px;
      right: 42px;
      @media screen and (max-width: $screen-xs) {
        top: 0;
        right: 0;
      }
    }
  }
}

.latest-items-box {
  display: inline-block;
  width: 100%;
  border-radius: 4px;
  background-color: $white;
  height: 100%;
  @media screen and (max-width: $screen-xs) {
    height: auto;
    margin-bottom: 30px;
  }
  .latest-link {
    border-bottom: 5px solid $border-color-white-smoke;
    padding: 0 30px;
    @media screen and (max-width: $screen-xs) {
      padding: 0 15px;
    }
    h3 {
      display: inline-block;
      padding: 20px 0;
      margin: 0;
      position: relative;
      text-transform: uppercase;
    }
    .link-all {
      float: right;
      padding: 20px 0;
      font-size: 1rem;
    }
  }

  .link-all-mbl {
    font-size: 1rem;
    float: left;
    padding: 8px 15px 15px;
  }

  .latest-items {
    padding: 15px 30px;
    @media screen and (max-width: $screen-xs) {
      padding: 15px;
    }
    .item {
      border-bottom: 2px solid $border-color-white-smoke;
      padding: 10px 0;
      margin: 0;
      &:first-of-type {
        padding-top: 0;
      }
      &:last-of-type {
        padding-bottom: 0;
        border-bottom: none;
      }
      a > h3 {
        margin: 0;
      }
    }
  }

  .events-box {
    .date,
    .info {
      background-color: transparent;
    }
    .date {
      padding-left: 0;
      span {
        display: inline-block;
        padding-top: 3px;
        padding-right: 5px;
      }
    }
    .info {
      padding-right: 0;
    }
  }

  .news-box {
    time {
      display: block;
      width: 100%;
      margin: 0 0 5px;
    }
  }
}

/*#endregion*/

/*#region Search Result Page Items*/
form[action*="Search-Results"] {
  @media screen and (min-width: $screen-sm) {
    overflow-x: hidden;
  }
}

.search-dialog {
  padding: 15px 0;
  background-color: $white;
  display: inline-block;
  width: 100%;
  position: relative;
  .editing-form-label-cell,
  .editing-form-value-cell {
    display: inline-block;
    font-size: 0;
    @media screen and (min-width: 0 \0
    ) {
      vertical-align: middle;
    }
    @media screen and (max-width: $screen-xs) {
      width: 100%;
    }
  }
  .form-horizontal {
    display: inline-block;
    width: 100%;
    &:after {
      position: absolute;
      display: block;
      content: "";
      width: 2400px;
      left: 50% !important;
      transform: translateX(-50%);
      bottom: 0;
      border-bottom: 2px solid $border-color-racing-green;
      @media screen and (max-width: $screen-xs) {
        width: 100vw;
      }
    }
    .form-group {
      margin-left: 0;
      margin-right: 0;
      margin-bottom: 0;
      float: left;
      @include behaveRow;
      &:first-of-type {
        width: 83%;
        @media screen and (max-width: $screen-xs) {
          width: 100%;
        }
      }
      &:last-of-type {
        width: 17%;
        @media screen and (max-width: $screen-xs) {
          width: 100%;
        }
      }
      .editing-form-value-cell {
        input[type="text"] {
          margin-bottom: 20px;
        }
      }
    }
    .control-label {
      font-size: 0.875rem;
      font-weight: 500;
      line-height: 38px;
      padding-top: 0;
      padding-right: 10px;
      margin: 0;
      @media screen and (max-width: $screen-xs) {
        display: none;
      }
    }
    .form-control {
      min-width: 600px;
      height: auto !important;
      padding: 8px 14px;
      margin-bottom: 0;
      border-radius: 0;
      border: 1px solid rgba(0, 97, 175, 0.2);
      @media screen and (max-width: $screen-xs) {
        min-width: 100%;
      }
      &:hover,
      &:focus {
        border: 1px solid $border-color-silver;
      }
    }
    .btn {
      text-transform: uppercase;
      min-width: 149px;
      padding: 8px 12px;
      @extend .btn-primary;
      @media screen and (max-width: $screen-xs) {
        min-width: 100%;
      }
    }
  }
}

.SearchResults {
  padding: 30px 0;
  .ContentLabel {
    font-weight: bold;
  }
}

.results-info {
  padding: 20px 0;
  border-bottom: 2px solid $border-color-white-smoke;
  p {
    margin-bottom: 0;
  }
  .path.hidden {
    display: none;
  }
}

.search-paging {
  font-size: 0;
  padding: 30px 0 0;
  @media screen and (max-width: $screen-xs) {
    padding: 20px 0;
  }
  .summary {
    width: 40%;
    display: inline-block;
    font-size: 1rem;
    font-weight: 400;
    line-height: 22px;
    @media screen and (max-width: $screen-xs) {
      width: 100%;
      margin-bottom: 20px;
    }
  }
  .links-wrapper {
    text-align: right;
    width: 60%;
    display: inline-block;
    @media screen and (max-width: $screen-xs) {
      position: relative;
      width: 100%;
      text-align: left;
    }
    a,
    .active-page {
      display: inline-block;
      font-size: 1rem;
      font-weight: 400;
      line-height: 24px;
      padding: 5px;
      margin: 0 5px;
      &.hidden {
        display: none;
        margin: 0 !important;
        padding: 0 !important;
        font-size: 0 !important;
      }
      @media screen and (max-width: $screen-xs) {
        display: none;
      }
    }
    a {
      color: $text-color-deep-sky-blue;
      &.icon-left,
      &.icon-right {
        padding: 0;
        position: relative;
        width: 35px;
        height: 35px;
        border-radius: 50%;
        font-size: 0;
        vertical-align: bottom;
        &:after {
          color: $text-color-deep-sky-blue;
          @include use-glyphicon;
          width: 15px;
          height: 15px;
          position: absolute;
          right: 0;
          bottom: 0;
          left: 0;
          top: 0;
          margin: auto;
          display: block;
          font-size: 1rem;
        }
        &.disabled {
          color: #acacac;
          cursor: default;
          &:after {
            color: #acacac;
          }
        }
        @media screen and (max-width: $screen-xs) {
          display: inline-block;
          margin: 0;
        }
      }
      &.icon-left {
        margin-left: 0;
        &:after {
          content: "\e079";
          @media screen and (max-width: $screen-xs) {
            left: 0;
            right: auto;
          }
        }
      }
      &.icon-right {
        margin-right: 0;
        @media screen and (max-width: $screen-xs) {
          float: right;
        }
        &:after {
          content: "\e080";
          @media screen and (max-width: $screen-xs) {
            right: 0;
            left: auto;
          }
        }
      }
    }
    .active-page {
      font-weight: 600;
      @media screen and (max-width: $screen-xs) {
        display: inline-block;
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        width: 15%;
        text-align: center;
      }
    }
  }
}

/*#endregion end*/

/*#region Common Items*/
.paging {
  list-style: none;
  padding: 0 15px;
  margin: 30px 0;
  @media screen and (max-width: $screen-xs) {
    margin: 15px 0;
    display: inline-block;
    width: 100%;
  }
  li {
    display: inline-block;
    padding: 0 9px;
    @media screen and (max-width: $screen-xs) {
      display: none;
    }
    a {
      padding: 6px 6px;
      display: inline-block;
      @include x-rem(font-size, 16px);
      @include line-height(16, 24);
      &.pn {
        color: $text-color-dim-gray;
        padding: 6px 0;
      }
      &.pn.icon-left,
      &.pn.icon-right {
        position: relative;
      }
      &.pn.icon-left:after,
      &.pn.icon-right:after {
        width: 10px;
        height: 12px;
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        content: "";
        background: url("imgs/oh-sprite-icons.png") no-repeat;
      }
      &.pn.icon-left {
        padding-left: 12px;
      }
      &.pn.icon-left:after {
        left: 0;
        right: auto;
        background-position: 0 -1px;
      }
      &.pn.icon-left.disabled {
        cursor: default;
        text-decoration: none;
        color: #acacac;
      }
      &.pn.icon-right {
        padding-right: 12px;
      }
      &.pn.icon-right:after {
        left: auto;
        right: 0;
        background-position: -19px -1px;
      }
    }
    &.pn.icon-right.disabled {
      color: #acacac;
      cursor: default;
      text-decoration: none;
    }
  }
  &.current {
    @media screen and (max-width: $screen-xs) {
      text-align: center;
    }
    a {
      padding: 6px 14px;
    }
  }
  &:first-of-type {
    padding-left: 0;
  }
  &:last-of-type {
    @media screen and (max-width: $screen-xs) {
      text-align: right;
    }
  }
  &:first-of-type,
  &:last-of-type,
  &.current {
    @media screen and (max-width: $screen-xs) {
      display: inline-block;
      width: 33%;
      padding: 0;
      float: left;
    }
  }
}

section {
  padding: 15px 0;
  &.carousel {
    padding: 0;
  }
  > *:last-child {
    margin-bottom: 0;
  }
}

.inner-main {
  padding: 15px 0;
}

.right-side-nav {
  margin: 0 0 30px;
  @media screen and (max-width: $screen-xs) {
    display: none;
  }
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      border-bottom: 1px solid $border-color-silver;
      display: inline-block;
      width: 100%;
      padding: 5px 0;
      a {
        font-size: 0.875rem;
        font-weight: 300;
        line-height: 1.3;
        padding: 7px 5px;
        display: inline-block;
        width: 100%;
      }
      &.active-item {
        padding: 0;
        > a {
          text-decoration: underline;
        }
      }
      &:first-of-type {
        border-top: 1px solid $border-color-silver;
      }
    }
  }
  > ul {
    //first-level
    > li {
      > ul {
        //second level
        > li {
          > a {
            font-size: 0.8125rem;
            padding-left: 15px;
          }
          &:first-of-type {
            padding-top: 0;
          }
          &:last-of-type {
            padding-bottom: 0;
            border-bottom: none;
          }
          > ul {
            //third level
            > li {
              > a {
                padding-left: 25px;
                font-size: 0.75rem;
              }
              &:last-of-type {
                border-bottom: none;
              }
              ul {
                //last level
                li {
                  a {
                    font-size: 0.625rem;
                    padding-left: 35px;
                  }
                }
              }
            }
          }
        }
      }
      //first-level active path
      &.active-path {
        padding: 0;
        > a {
          font-weight: 700;
        }
      }
    }
  }
}

.input-error {
  input:not([type="submit"]):not([type="button"]),
  textarea {
    border: 1px solid #e02a26;
  }
  span {
    color: #e02a26;
  }
}

.zero-xs-margin {
  @media screen and (max-width: $screen-xs) {
    margin-right: 0;
    margin-left: 0;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  a {
    color: inherit;
    font-size: inherit;
    line-height: inherit;
    &:hover,
    &:focus,
    &:active {
      color: inherit;
    }
  }
}

main {
  p.gallery-intro {
    padding-bottom: 30px !important;
    border-bottom: 2px solid $border-color-white-smoke;
  }
  p.map-text {
    @include x-rem(font-size, 18px);
    @include line-height(16, 18);
    color: $text-color-dim-gray;
    text-transform: uppercase;
    padding: 23px 0 17px;
    @media screen and (max-width: $screen-xs) {
      display: none;
    }
  }
  a.external-link {
    padding-right: 20px;
    &:after {
      content: "";
      background: url("imgs/oh-sprite-icons.png") no-repeat;
      background-position: -100px -85px;
      height: 15px;
      width: 15px;
      display: inline-block;
      margin-left: 5px;
    }
  }
}

.ContentPadding {
  padding: 0 15px;
}

.breadcrumb-wrapper {
  nav,
  .details-back {
    display: inline-block;
  }
  .details-back {
    float: right;
    margin-top: 8px;
    @media screen and (max-width: $screen-xs) {
      float: inherit;
    }
  }
}

/*#endregion end*/

/*#region ADMIN ISSUES*/
.spotlight-area-wrapper {
  .WebPart {
    width: 33.33333%;
    float: left;
    .col-sm-12 {
      width: 100%;
      float: none;
    }
  }
  .WebPartContent .col-sm-4 .wrapper {
    min-width: 300px;
  }
}

/*#endregion end*/