//screen sizes
$max-container: 480px;
$screen-xs: 640px;
$screen-sm: 641px;
$screen-md: 1199px;
$mac-port: 768px;
$mac-land: 1024px;
$min-width-on-scaled-down: 1200px;
$base-font-size: 1rem;

// Line height
$line-height-base: 1.6;
// Other Sizes
$base-spacing: $line-height-base * 1em;
$small-spacing: $base-spacing / 2;
$base-z-index: 0;

// Base Colors

/*
For naming convention of color variable I have used this site
https://www.htmlcsscolor.com/hex/262928
we have to place the hex code of color after the last slash (/), It give a nice name for the color
*/

$white: #ffffff;
$racing-green: #262928;
$dim-gray: #6a6a6a;
$silver: #bababa;
$white-smoke: #f3f1f1;
$deep-sky-blue: #03a6ff;
$polo-blue: #79a3c2;
$ny-pink: #de7e73;

$text-color-white: $white;
$border-color-white: $white;
$bg-color-white: $white;
$shadow-color-white: $white;

$text-color-racing-green: $racing-green;
$border-color-racing-green: $racing-green;
$bg-color-racing-green: $racing-green;

$text-color-dim-gray: $dim-gray;
$bg-color-dim-gray: $dim-gray;

$border-color-silver: $silver;
$bg-color-silver: $silver;

$bg-color-white-smoke: $white-smoke;
$border-color-white-smoke: $white-smoke;
$shadow-color-white-smoke: $white-smoke;

$text-color-deep-sky-blue: $deep-sky-blue;
$bg-color-deep-sky-blue: $deep-sky-blue;

$text-color-polo-blue: $polo-blue;
$bg-color-polo-blue: $polo-blue;

$text-color-ny-pink: $ny-pink;
$bg-color-ny-pink: $ny-pink;

// header
$nav-header-bg-color: #FFFFFF;
$nav-header-bg-light-color: #f3f1f1;

// navigation links
$nav-header-link-color: #262928;
$nav-header-link-active-color: #03a6ff;
$nav-border-color: #bababa;

//TNS Links
$tns-link-color: #6a6a6a;