a.spotlight.hover-overlay {
  .image-wrapper {
    &:before {
      opacity: 0;
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 1;
      @include mxTransition(0.3s);
    }
  }
  &:hover,
  &:focus,
  &:active {
    outline: 0;
    .image-wrapper {
      &:before {
        opacity: 1;
      }
      .overlay-box,
      .overlay-text-box,
      .overlay-icon-box {
        z-index: 2;
      }
    }
  }
}

//image zoom effect
.spotlight.image-zoom-effect {
  .image-wrapper {
    overflow: hidden;
    margin: 0.05%;
    .overlay-text-box{
      z-index: 2;
    }
  }
}