.annotated-spot {
  .annotated-bg {
    box-shadow: 0 3px 21px 0 rgba(32, 32, 32, 0.26) inset;
    background: url("imgs/interactive-annotated-image-grouped-hotspot/map-bg.png") no-repeat center center;
    background-color: $bg-color-white;
    @media screen and (max-width: $screen-xs) {
      background: none;
      box-shadow: none;
    }
    .pins-wrapper {
      .map-pins {
        > span {
          background-color: $bg-color-ny-pink;
          border-radius: 50%;
          font-size: 0.75rem;
          cursor: pointer;
        }
        .map-tip .tooltip-inner {
          background-color: $bg-color-white-smoke;
          box-shadow: 0 1px 4.8px 0.2px rgba(48, 112, 146, 0.75);
          &:before {
            content: "";
            background: url("imgs/interactive-annotated-image-grouped-hotspot/tooltip-arrow.png") no-repeat center center;
          }
        }
        .map-tip .map-pin {
          span {
            color: $text-color-racing-green;
            &:last-of-type:before {
              content: "";
              background: url("imgs/interactive-annotated-image-grouped-hotspot/map-tooltip-arrow.png") no-repeat center center;
            }
          }
          &:hover,
          &:focus,
          &:active {
            outline: none;
          }
        }
        .map-tip.fade.in {
          opacity: 1;
        }
        .map-tip.tooltip.right {
          margin-left: 8px;
        }
      }
    }
    .collapseable-wrapper {
      ul {
        li {
          a {
            background-color: $bg-color-deep-sky-blue;
            color: $text-color-white;
          }
        }
      }
    }
  }
}
