#nav_drop_down {
  #gns_drop_down.separate-links {
    > li.dropdown {
      font-size: 0;
      > a.dropdown-arrow {
        &:hover,
        &:focus,
        &:active {
          text-decoration: none;
          border-color: transparent;
        }
      }
      &.open {
        > a.dropdown-arrow {
          border-bottom-color: transparent;
        }
      }
    }
  }
  &.expand-on-touch {
    #gns_drop_down {
      > li > a {
        &:hover,
        &:focus,
        &:active {
          &:before {
            background-color: $nav-border-color;
            @media screen and (max-width: $screen-xs) {
              display: none;
            }
          }
        }
      }
      &.open {
        > a {
          border-bottom: none;
        }
      }
      &.active {
        a {
          &:hover,
          &:focus,
          &:active {
            &:before {
              background-color: $nav-header-link-active-color;
            }
          }
        }
      }
    }
  }
}