#wrapper {
  height: 100%;
  &.slide-left {
    @media screen and (max-width: $screen-xs) {
      @include mxTransition(0.5s);
      @include prefix(transform, translateX(-84%));
      overflow: hidden;
    }
  }
  &.slide-back {
    @media screen and (max-width: $screen-xs) {
      @include mxTransition(0.5s);
      @include prefix(transform, translateX(0%));
    }
  }
}

#nav_drop_down {
  position: relative;
  @media screen and (max-width: $screen-xs) {
    min-height: 1px;
  }
  .logo-column {
    float: left;
    width: 17.6%;
    padding: 12px 15px 4px 2px;
    @media screen and (max-width: $screen-xs) {
      width: 60%;
      padding: 10px 0;
    }
    .logo-link {
      display:block;
      margin: 22px 0;
      padding: 0 0 0 15px;
      @media screen and (max-width: $screen-xs) {
        margin: 0;
      }
    }
  }
  .mobile-menu {
    float: left;
    width: 40%;
    padding: 25px 0 25px 15px;
    @media screen and (max-width: $screen-xs) {
      padding: 20px 0;
    }
    button {
      width: 40px;
      height: 40px;
      margin: auto;
      &:hover,
      &:focus,
      &:active {
        outline: none;
      }
    }
    #menu_drop_down {
      margin-right: 15px;
      @include mxTransition(0.2s);
      @media screen and (max-width: $screen-xs) {
        margin-left: 10px;
      }
      &.open {
        margin-right: 5px;
      }
    }
  }
  .nav-column {
    float: left;
    width: 82.4%;
    padding: 15px 15px 0 15px;
    @media screen and (max-width: $screen-xs) {
      padding: 15px 0;
      display: none;
      z-index: 999;
      position: absolute;
      top: 0;
      left: 100%;
      width: 84%;
      overflow-y: auto;
    }
    > ul {
      width: 100%;
      padding: 0;
      > li {
        display: inline-block;
      }
    }
    &.open {
      @media screen and (max-width: $screen-xs) {
        @include swapItems("#gns_drop_down", "#tns_drop_down");
        overflow-y: auto;
        min-height: 100vh;
      }
    }
  }
}

#tns_drop_down {
  margin: 5px 0 30px;
  @media screen and (max-width: $screen-xs) {
    margin: 0;
  }
  > li {
    padding: 0 15px;
    @media screen and (max-width: $screen-xs) {
      display: block;
      width: 100%;
      padding: 5px 15px;
    }
    &.call-back {
      @media screen and (max-width: $screen-xs) {
        padding: 12px 15px;
        margin: 10px 0;
      }
      a {
        @media screen and (max-width: $screen-xs) {
          display: block;
          padding: 2px 0;
        }
      }
    }
    &:last-of-type {
      padding-right: 0;
      margin-top: 5px;
    }
    &:first-of-type {
      padding-left: 0;
      @media screen and (max-width: $screen-xs) {
        padding-left: 15px;
      }
    }
  }
}

#gns_drop_down {
  margin: 0;
  li,
  a {
    @media screen and (max-width: $screen-xs) {
      display: block;
      width: 100%;
    }
  }
  > li {
    padding: 0 15px;
    @media screen and (max-width: $screen-xs) {
      padding: 5px 15px;
    }
    > a {
      padding: 0;
    }
    &.dropdown {
      padding-right: 30px;
      @media screen and (max-width: $screen-xs) {
        padding-right: 15px;
      }
      > a {
        position: relative;
        &:after {
          display: none;
        }
        span.down-arrow {
          display: inline-block;
          width: 12px;
          height: 8px;
          position: absolute;
          top: 0;
          right: -15px;
          bottom: 0;
          left: auto;
          margin: auto;
          @include mxTransition(0.5s);
          @media screen and (max-width: $screen-xs) {
            right: 0;
          }
        }
      }
      .dropdown-menu {
        margin: 0;
        padding: 14px 0;
        width: 100%;
        //top: 35px;
        &:after {
          bottom: 100%;
          left: 50%;
          content: "";
          height: 0;
          width: 0;
          position: absolute;
          pointer-events: none;
          margin-left: -6px;
          @media screen and (max-width: $screen-xs) {
            display: none;
          }
        }
        @media screen and (max-width: $screen-xs) {
          position: relative;
          top: 0;
          padding: 0;
          margin: 0;
        }
        > li {
          padding: 0;
          > a {
            padding: 5px 10px;
            white-space: normal;
            @media screen and (max-width: $screen-xs) {
              padding: 8px 0;
            }
          }
          > ul {
            display: none;
            @media screen and (max-width: $screen-xs) {
              display: block;
              width: 100%;
              padding: 0 8px;
            }
            > li {
              > a {
                padding: 4px 0;
              }
            }
          }
        }
      }
      &.show {
        /*padding-bottom: 14px;
        @media screen and (max-width: $screen-xs) {
          padding-bottom: 5px;
        }*/
        > a {
          span.down-arrow {
            transform: rotate(-180deg);
          }
        }
      }
    }
    &:first-of-type {
      padding-left: 0;
      @media screen and (max-width: $screen-xs) {
        padding-left: 15px;
      }
    }
    &:last-of-type {
      padding-right: 0;
      @media screen and (max-width: $screen-xs) {
        padding-right: 15px;
      }
      &.dropdown {
        padding-right: 15px;
        @media screen and (min-width: $screen-sm) and (max-width: $screen-md) {
          .dropdown-menu {
            right: 0;
            left: auto;
            &:after {
              left: auto;
              right: 40px;
            }
          }
        }
        @media screen and (max-width: $screen-xs) {
          padding-right: 15px;
        }
      }
    }
  }
}

@import "structure_expand_hover";
@import "structure_search_menu";
