.multi-item-carousel {
  .carousel-inner {
    &:before,
    &:after {
      background-color: transparent;
    }
    .spotlight {
      background-color: $bg-color-white;
      .overlay-icon-text {
        background-color: rgba(0, 0, 0, 0.6);
        color: $text-color-white;
        font-size: 0.75rem;
      }
    }
  }
  .control {
    color: $text-color-racing-green;
  }
}

