.rg-gallery {
  background-color: $bg-color-white-smoke;
  .rg-thumbs {
    .es-carousel-wrapper .es-carousel ul li {
      a {
        opacity: 0.8;
        -webkit-touch-callout: none;
        @include mxTransition(all 0.2s ease-in-out);
        img {
          border: none;
          opacity: 0.7;
        }
      }
      &.selected {
        a {
          opacity: 1;
          img {
            opacity: 1;
          }
        }
      }
    }
    .es-nav {
      span {
        background: transparent url("imgs/gallery/nav_thumbs.png") no-repeat center left;
        text-indent: -9000px;
        cursor: pointer;
        opacity: 0.8;
        &.es-nav-next {
          background-position: center right;
        }
        &:hover {
          opacity: 1;
        }
      }
    }
  }
  .rg-image-wrapper {
    .rg-image-nav {
      .rg-image-nav-prev,
      .rg-image-nav-next {
        color: $text-color-white;
        background-color: $bg-color-deep-sky-blue;
      }
    }
  }
}