.rg-gallery {
  margin-top: 30px;
  margin-bottom: 30px;
  width: 100%;
  .rg-thumbs {
    .es-carousel-wrapper {
      padding: 20px 26px;
      min-height: 110px;
      position: relative;
      @media screen and (max-width: $screen-xs) {
        min-height: 1px;
      }
      .es-carousel {
        overflow: hidden;
        ul {
          display: none;
          padding: 0;
          float: left;
          margin: 0;
        }
        li {
          height: 100%;
          float: left;
          display: block;
          a {
            display: block;
          }

          img {
            display: block;
            min-width: 120px;
            max-width: 120px;
            max-height: 71px;
          }
        }
      }
      .es-nav {
        span {
          position: absolute;
          top: 50%;
          left: 6px;
          width: 16px;
          height: 93px;
          margin-top: -48px;
          &.es-nav-next {
            right: 4px;
            left: auto;
          }
        }
      }
    }
  }
  .rg-image-wrapper {
    position: relative;
    .rg-image {
      img {
        display: block;
        width: 100%;
        height: auto;
      }
    }
    .rg-image-nav {
      position: absolute;
      right: 1%;
      bottom: 2.5%;
      @media screen and (max-width: $screen-xs) {
        position: relative;
        right: 0;
        bottom: 0;
        margin-top: 15px;
        text-align: right;
        margin-right: 10px;
      }
      .rg-image-nav-prev,
      .rg-image-nav-next {
        padding: 8px 30px;
        margin: 0 2px;
        @media screen and (max-width: $screen-xs) {
          display: inline-block;
        }
        &:hover {
          text-decoration: none;
        }
      }
    }
    .rg-caption-wrapper {
      @include behaveRow;
      .rg-caption {
        float: left;
        width: 75%;
        display: block;
        padding: 20px 4px;
        @media screen and (max-width: $screen-xs) {
          width: 100%;
          float: none;
        }
      }
      .rg-image-nav {
        float: left;
        width: 20%;
        padding: 12px 7px 18px;
        display: block;
        text-align: right;
        .rg-image-nav-prev,
        .rg-image-nav-next {
          padding: 5px 30px;
          display: inline-block;
        }
        .rg-image-nav-prev {
          margin-right: 5px;
        }
      }
    }
  }
}